.input {
    position: relative;

    &__control {
        outline: none;
        padding: 8px 25px 8px 16px;
        border: 0.5px solid $border-color;
        border-radius: 4px;
        min-width: 241px;

        &_full-width {
            width: 100%;
        }

        &:focus {
            @include setAlpha(border-color, $violet, 1);
        }
        
        &_error {
            border: 0.5px solid $error-border-color;
            &:focus {
                @include setAlpha(border-color, $error-border-color, 1);
            }
            &:before {
                content: "♥";// attr(data-error);
                width: 20px;
                height: 20px;
            }
        }
    }

    &__icon {
        width: 16px;
        height: 16px;
        position: absolute;
        top: 50%;
        right: 9px;
        transform: translateY(-50%);
    }
}